<template>

    <v-container
            fluid
            style="min-height: 0;"
            grid-list-lg
    >
    <v-layout row wrap>
        <v-flex xs12>
            <v-card color="grey lighten-3" class="white--text elevation-14">

                <v-flex xs10 offset-xs1>
                    <v-card dark color="grey lighten-3" class="black--text elevation-0" >

                        <v-list-tile @click="">
                            <v-list-tile-avatar>
                                <img :src=imgPath>
                            </v-list-tile-avatar>
                            <v-list-tile-content>
                                <v-list-tile-title v-html=interpret.users.name></v-list-tile-title>
                                <!--<v-list-tile-sub-title v-html=interpret.users.name></v-list-tile-sub-title>-->
                            </v-list-tile-content>
                        </v-list-tile>
                    </v-card>
                </v-flex>


                <v-card-title primary-title >


                    <v-flex xs10 offset-xs1>
                        <v-card dark color="grey lighten-3">
                            <v-card-text>

                                <span class="left black--text" style="font-weight: bold">
                                    Passagem biblica:
                                     </span>
                            <span class="black--text elementEs" >
                             <v-icon  color="deep-orange darken-1" left>bookmark</v-icon>
                                Livro: {{interpret.biblic_references.books.designacao}}
                            </span>
                                <!--<br>-->
                                        <span class="black--text text-sm-left elementEs ">
                                <v-icon color="deep-orange darken-1" left>bookmark</v-icon>
                                De capitulo: {{interpret.biblic_references.de_capitulo}} a {{interpret.biblic_references.a_capitulo}}
                            </span>
                                <!--<br>-->
                                <span class="black--text text-sm-left elementEs">
                                <v-icon  color="deep-orange darken-1" left>bookmark</v-icon>
                                De versiculo: {{interpret.biblic_references.de_versiculo}} a {{interpret.biblic_references.a_versiculo}}
                            </span>

                                <p class="justify black--text" style="font-weight: 200; font-style: italic">
                                  " {{interpret.biblic_references.descricao}} "
                                 </p>
                                 <!--<span style="text-align: justify">-->
                                    <!--" {{interpret.biblic_references.descricao}} "-->
                                <!--</span>-->
                            </v-card-text>

                        </v-card>
                    </v-flex>
                    <v-flex xs10 offset-xs1>
                        <v-card dark color="gray">
                            <v-card-text><span class="left white--text" style="font-weight: bold">Interpretação:</span>
                                <br>
                                <span class="justify">
                                {{interpret.descricao}}
                            </span></v-card-text>
                        </v-card>
                    </v-flex>
                    <v-flex xs10 offset-xs1>
                        <v-card dark color="grey lighten-3" class="black--text elevation-0 right">
                            <v-card-text class="black--text"> postado a: {{ moment(interpret.created_at).fromNow() }}</v-card-text>
                        </v-card>
                    </v-flex>
                    <!--<div class="headline" ><span class="deep-orange&#45;&#45;text element" style="font-style: italic"></span></div>-->
                    <!--<div class="text-sm-left"><span class="black&#45;&#45;text element" ></span></div>-->
                    <!--<div><span class="black&#45;&#45;text element" ></span></div>-->
                </v-card-title>
                <v-card-actions >
                    <v-flex xs12 sm1 offset-xs0 offset-md8  >
                        <v-btn flat icon color="blue darken-2" >
                            <v-badge left>
                                <span slot="badge"  class="element">{{comments}}</span>
                                <v-icon color="teal darken-2" >fas fa-comments</v-icon>
                            </v-badge>

                        </v-btn>
                    </v-flex>
                    <v-flex xs12 sm1 offset-xs0  >
                        <v-btn flat icon color="blue darken-2" @click="onLike(1)" >
                            <v-badge left>
                                <span slot="badge" class="element">{{likes}}</span>
                                <v-icon   >thumb_up</v-icon>
                            </v-badge>
                        </v-btn>
                    </v-flex>

                    <v-flex xs12 sm1>
                        <v-btn flat icon color="orange darken-4" @click="onLike(0)">
                            <v-badge left>
                                <span slot="badge"  class="element">{{deslikes}}</span>
                                <v-icon>thumb_down</v-icon>
                            </v-badge>
                        </v-btn>
                    </v-flex>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
    </v-container>
</template>

<script>
    import axios from 'axios';

    var moment = require('moment');
    export default {

        props:[
            'interpret'
        ],
        data(){
            return{
                userName:'',
//                imgPath:'https://api.adorable.io/avatars/137/abott@adorable.png',
                imgPath:this.interpret.users.foto,
                moment:moment,
                editing:false,
                editvalue: this.interpret.descricao,
                comments:0,
                likes:0,
                deslikes:0,
                date:''
            }
        },
        methods:{
            onLike(tipo){

                let agree={
                    'state':1,
                    'users_id':5,
                    'interpretations_id':this.interpret.id
                };

                if (tipo==0){
                   agree.state=0;
                }


                axios.post('http://localhost:8000/api/interpretation-agreements',agree)
                    .then(
                        response=> {
                            console.log(response);
                           if (tipo==0){
                               this.deslikes++;
                           }else {
                               this.likes++;
                           }
                        }
                    )
                    .catch(
                        error=>console.log(error)
                    )
            },
            onEdit(){
                this.editing=true;
                this.editvalue= this.interpret.descricao;

            },
            onCancel(){
                this.editing=false;

            },
            onDelete(){

                const token = localStorage.getItem('token');

                this.$emit('quoteDeleted',this.interpret.id);
                axios.delete('http://localhost:8000/api/interpretations/' + this.interpret.id+'?token='+token)
                    .then(
                        response=>console.log(response)
                    )
                    .catch(
                        error=>console.log(error)
                    )
            },
            onSave(){
                const token = localStorage.getItem('token');

                this.editing=false;
                this.interpret.descricao=this.editvalue;
                axios.put('http://localhost:8000/api/interpretations/' + this.interpret.id+'?tpken='+token,
                    {descricao:this.editvalue})
                    .then(
                        response=>console.log(response)
                    )
                    .catch(
                        error=>console.log(error)
                    )
            }

        },

        created(){


            this.userName=this.interpret.users.name;
            axios.get('http://localhost:8000/api/interpretations-count/'+this.interpret.id)
                .then(
                    response=> {
                        console.log(response);
                        this.comments = response["data"]["comments"],
                            this.likes = response["data"]["likes"],
                            this.deslikes = response["data"]["deslikes"]
                    }
                )
                .catch(
                    error=>console.log(error)
                )
        },
        filters: {
            moment: function (date) {
                return moment(interpret.created_at,date).fromNow();

            }
        }
    }
</script>

<style scoped="">

    .element{
        font-size:8pt;
        float: left;
    }
    .elementEs{
        font-size:9pt;
        /*float: right;*/
    }
    .titulos{
        font-size:12pt

    }
</style>


