<template>

    <form @submit.prevent="onSubmetted">
        <div class="form-group">
            <label for="content"> Content</label>
            <input type="text" id="content" class="form-control" v-model="quoteContent">
        </div>

        <button class="btn btn-primary" type="submit">add new quote</button>
    </form>

</template>

<script>

    import axios from 'axios';
    export default {
        data(){
            return{
                quoteContent:''
            }
        },
        methods:{
            onSubmetted(){

                const token = localStorage.getItem('token');
                axios.post('http://localhost:8000/api/quote?token='+token,
                    {content:this.quoteContent}
                ).then(
                    (response)=>console.log(response)
                )
                    .catch(
                        (error)=>console.log(error)
                    );

            }
        }
    }
</script>


