import Vue from 'vue'
import Router from 'vue-router'
import Interpretations from '@/components/pagina-inicial/interpretations.vue'
import NewQuote from '@/components/pagina-inicial/new-quote.vue'
import Signup from '@/components/User/signup.vue'
import  Signin from '@/components/User/signin.vue'
import  Profile from '@/components/User/profile.vue'
import  MostAgreed from '@/components/interpretations/most-agreed-interpretations'
import  MostViewed from '@/components/interpretations/most-viewed-interpretations'
import  MostAsked from '@/components/interpretations/most-asked-questions'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Interpretation',
      component: Interpretations
    },
      {path:'/',component:Interpretations},
      {path:'/new-quote',component:NewQuote},
      {path:'/user/signup',component:Signup},
      {path:'/user/signin',component:Signin},
      {path:'/user/profile',component:Profile},
      {path:'/interpretations/most-agreed',component:MostAgreed},
      {path:'/interpretations/most-viewed',component:MostViewed},
      {path:'/interpretations/most-asked',component:MostAsked},

  ],
    mode:'history'
})



