<template>
    <v-layout row>
        <v-flex xs12 sm10 offset-sm1>
            <v-card>
                <v-toolbar color="pink darken-1" dark>
                    <!--<v-toolbar-side-icon></v-toolbar-side-icon>-->
                    <v-toolbar-title class="titulos">Most Followed</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <!--<v-btn icon>-->
                        <!--<v-icon>search</v-icon>-->
                    <!--</v-btn>-->
                    <!--<v-btn icon>-->
                        <!--<v-icon>check_circle</v-icon>-->
                    <!--</v-btn>-->
                </v-toolbar>
                <v-list two-line >
                    <template v-for="(item, index) in items">
                        <v-list-tile
                                avatar
                                ripple
                                @click="toggle(index)"
                                :key="item.title"

                        >
                            <v-list-tile-content class="element">
                                <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                                <v-list-tile-sub-title class="text--primary">{{ item.headline }}</v-list-tile-sub-title>
                                <v-list-tile-sub-title>{{ item.subtitle }}</v-list-tile-sub-title>
                            </v-list-tile-content>
                            <v-list-tile-action>
                                <v-list-tile-action-text>{{ item.action }} followers</v-list-tile-action-text>
                                <v-icon
                                        color="grey lighten-1"
                                        v-if="selected.indexOf(index) < 0"
                                >star_border</v-icon>
                                <v-icon
                                        color="yellow darken-2"
                                        v-else
                                >star</v-icon>
                            </v-list-tile-action>
                        </v-list-tile>
                        <v-divider v-if="index + 1 < items.length" :key="index"></v-divider>
                    </template>
                </v-list>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    export default {
        data () {
            return {
                selected: [2],
                items: [
                    { action: '1100', headline: 'Brunch this weekend?', title: 'Ali Connors', subtitle: "I'll be in your neighborhood doing errands this weekend. Do you want to hang out?" },
                    { action: '900', headline: 'Summer BBQ', title: 'me, Scrott, Jennifer', subtitle: "Wish I could come, but I'm out of town this weekend." },
                    { action: '888', headline: 'Oui oui', title: 'Sandra Adams', subtitle: 'Do you have Paris recommendations? Have you ever been?' },
                    { action: '743', headline: 'Birthday gift', title: 'Trevor Hansen', subtitle: 'Have any ideas about what we should get Heidi for her birthday?' },
                    { action: '603', headline: 'Recipe to try', title: 'Britta Holt', subtitle: 'We should eat this: Grate, Squash, Corn, and tomatillo Tacos.' }
                ]
            }
        },

        methods: {
            toggle (index) {
                const i = this.selected.indexOf(index)

                if (i > -1) {
                    this.selected.splice(i, 1)
                } else {
                    this.selected.push(index)
                }
            }
        }
    }
</script>
<style scoped="">

    .element{
        font-size:8pt
    }
    .titulos{
        font-size:12pt

    }
</style>


