<template>
    <div>
        <!--<button class="btn btn-primary" @click="onGetQuotes"> getQUotes</button>-->
        <br>
        <v-text>teste</v-text>
        <br>
        <!--<app-interpretation v-for="interpretation in interpretations"  v-bind:interpret="interpretation" @quoteDeleted = "onQuoteDeleted($event)"></app-interpretation>-->
    </div>
</template>

<script>
//    import Quote from './quote.vue'
    import Interpretation from '../pagina-inicial/interpretation.vue'
    import axios from 'axios'
    export default {

        data() {
            return {
                interpretations: []
            }

        },
        components: {
            'app-interpretation': Interpretation
        },
        methods: {},

            created() {
                axios.get('http://127.0.0.1:8000/api/interpretations').then(
                    response => {

                        console.log(response["data"]["interpretations"]);
                        this.interpretations = response["data"]["interpretations"];
                        console.log(this.interpretations.interpretation_agreements);

                    },
                ).catch(
                    error => console.log(error)
                );

            }

    }
</script>


