<template>
    <v-layout row>
        <v-flex>
            <v-card>
                <v-toolbar >
                    <!--<v-toolbar-side-icon></v-toolbar-side-icon>-->
                    <v-toolbar-title class="titulos">More Options</v-toolbar-title>
                    <v-spacer></v-spacer>
                </v-toolbar>

                <v-list>
                    <v-list-tile avatar @click="">
                        <v-list-tile-action>
                            <v-icon color="deep-orange darken-2" class="element">fas fa-plus</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content class="element">
                            <v-list-tile-title @click="openModal">
                                New post
                            </v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <v-list-tile avatar @click="">
                        <v-list-tile-action>
                            <v-icon color="deep-orange darken-2" class="element">fas fa-plus</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content class="element">
                            <v-list-tile-title @click="openModalQuestion">
                                New Question
                            </v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>


                    <v-list-tile avatar @click="" v-for="item in menuitens" :key="item.title" router :to="item.link">
                        <v-list-tile-action>
                            <v-icon color="deep-orange darken-2" class="element">fas fa-question-circle</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content class="element">
                            <v-list-tile-title @click="item.event">
                                {{item.title}}
                            </v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>

    import NewPost from './new-post.vue';
    import {bus} from '../../../src/main';

    export default {
        components: {
            'app-new-post': NewPost,

        },

        data () {
            return {
                menuitens: [
//
                    { icon:'',title: '+ viewed' , link:'/interpretations/most-viewed'},
                    { icon:'',title: '+ agreed', link:'/interpretations/most-agreed' },
                    { icon:'',title: '+ asked', link:'/interpretations/most-asked'}
                ]
            }
        },
        methods:{
            openModal(){

                console.log('entramos');
                bus.$emit('openDialog',true);
                console.log('terminamos');

            },
            openModalQuestion(){

                console.log('entramos');
                bus.$emit('openDialogQuestion',true);
                console.log('terminamos');

            }

        }
    }
</script>

<style scoped="">

    .element{
        font-size:8pt
    }
    .titulos{
        font-size:12pt

    }
</style>


